<template>
  <div>
    <van-sticky>
      <div class="head">{{ title[activeKey].name }}</div>
    </van-sticky>
    <div
      class="head-img-box"
      :style="{
        backgroundImage: `url(${getone[0]})`
      }"
    >
      <img
        :src="`https://images.weserv.nl/?url=https://jyacgpic.jyacg.com/app-pic/ranklist/${title[activeKey].img}.png`"
        alt=""
      />
      <div>《{{ getone[1] }}》</div>
      <div>{{ getone[2] }} 著</div>
    </div>

    <div class="below">
      <van-sticky :offset-top="40">
        <a href="#">
          <van-sidebar v-model="activeKey" class="le">
            <van-sidebar-item
              :title="item.name"
              v-for="item of title"
              :key="item.img"
            />
          </van-sidebar>
        </a>
      </van-sticky>
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        class="re"
      >
        <van-cell v-for="item in getrightlist" :key="item.id" class="cell" @click="$bus.$emit('details', item.id)">
          <img :src="item.cover_image" />
          <div class="cell-box">
            <div class="cell-box-s">{{ item.name }}</div>
            <div class="cell-box-z">
              <div class="cell-box-z-z">
                <div class="cell-box-z-noe">
                  <img :src="item.author_avatar" />
                  <span>{{ item.author }} 著</span>
                </div>
                <div class="cell-box-z-two">{{ item.book_label }}</div>
              </div>
            </div>
            <div class="cell-box-x">{{ item.total_words }} 万字</div>
          </div>
        </van-cell>
      </van-list>
    </div>
  </div>
</template>
  
  <script>
import axios from "axios";
export default {
  name: "MyrankList",
  components: {},
  data() {
    return {
      activeKey: 0,
      title: [
        { name: "综合榜", img: "zh", url: 1 },
        { name: "热销榜", img: "rx", url: 2 },
        { name: "新书榜", img: "xs", url: 3 },
        { name: "完本榜", img: "wb", url: "rqwb" },
        { name: "VIP榜", img: "vip", url: "viprm" },
        { name: "免费版", img: "mf", url: "mfrm" },
        { name: "月票榜", img: "yp", url: "ypzg" },
        { name: "收藏版", img: "sc", url: "sczb" },
        { name: "勤更榜", img: "qg", url: "zjgx" },
      ],
      rightlist: [],
      page: 1,
      finished: false,
      loading: false,
    };
  },
  computed: {
    getone(){
      if(this.rightlist.length){
        return [this.rightlist[0].cover_image,this.rightlist[0].name,this.rightlist[0].author]
      }
      return ['https://images.weserv.nl/?url=https://jyacgpic.jyacg.com/2021-05-15/443f1d63ebde16605804de14cea0c4c2.jpg']
    },
    gethttp() {
      if (this.activeKey == 6 || this.activeKey == 7) {
        return `https://jyapi.jyacg.com/web/book_room?tag=${
          this.title[this.activeKey].url
        }&page=${this.page}&limit=10`;
      }
      return `https://jyapi.jyacg.com/web/rank_list?id=${
        this.title[this.activeKey].url
      }&page=${this.page}&limit=10`;
    },
    getrightlist() {
      return this.rightlist.map((item) => {
        if (!item.book_label) {
          if (item.labels) {
            item.book_label = item.labels[0].name;
          }
        }
        if (!item.author_avatar) {
          if (!item.author_info) {
            item.author_avatar =
              "https://jyapi.jyacg.com/img/default_avatar.jpg";
          } else if (item.author_info.avatar) {
            item.author_avatar = item.author_info.avatar;
          } else {
            item.author_avatar =
              "https://jyapi.jyacg.com/img/default_avatar.jpg";
          }
        }
        return item;
      });
    },
  },
  watch: {
    activeKey() {
      this.page = 1;
      this.finished = false;
      this.onLoad();
    },
    loading() {
      if (this.loading) {
        this.onLoad();
      }
    },
  },
  methods: {
    onLoad() {
      axios.get(this.gethttp).then((res) => {
        if (this.page == 1) {
          this.rightlist = res.data.data;
          this.page++;
        } else {
          this.page++;
          this.rightlist.push(...res.data.data);
        }
        if (res.data.data.length == 0) {
          this.finished = true;
          return;
        }
        this.loading = false;
      });
    },
  },
  mounted() {},
  activated() {
    this.$bus.$emit("active", 2);
  },
};
</script>
<style scoped lang="scss">
.head-img-box {
  height: 208px;
  width: 100%;
  position: relative;
  overflow: hidden;
  background-size: cover;
  background-position: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  div{
    z-index: 1;
  }
  &::after {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;

    background-color: #333;
    opacity: 0.5;
  }
  img {
    margin-top: 3px;
    z-index: 1;
    width: 150px;
    height: 60px;
  }
  div{
    margin-top: 3px;
    font-size: 14px;
    color: #fff;
  }
}
.head {
  width: 100%;
  background-color: #fff;
  color: #333;
  font-weight: bold;
  font-size: 20px;
  line-height: 50px;
  text-align: center;
  height: 0px;
  overflow: hidden;
}
.van-sticky--fixed > .head {
  height: 50px;
}
.below {
  display: flex;
  justify-content: flex-end;
  .van-sidebar {
    
    width: 80px;
    padding: 20px 10px;
    a {
      border-radius: 10px;
      width: 80px;
      height: 40px;
      padding: 5px 10px;
      margin: 0 0 20px 0;
      line-height: 30px;
      text-align: center;

      div {
        font-size: 14px;
      }
    }
    .van-sidebar-item--select {
      box-shadow: 0px 0px 5px rgba(0, 0, 0, .1) ; 
      color: #fff;
    }
    a:nth-child(1).van-sidebar-item--select {
      background: #a5a8b2;
    }
    a:nth-child(2).van-sidebar-item--select {
      background: #5fa7ec;
    }
    a:nth-child(3).van-sidebar-item--select {
      background: #d6bb95;
    }

    a:nth-child(4).van-sidebar-item--select {
      background: #65dadf;
    }
    a:nth-child(5).van-sidebar-item--select {
      background: #6d638b;
    }
    a:nth-child(6).van-sidebar-item--select {
      background: #0a172c;
    }
    a:nth-child(7).van-sidebar-item--select {
      background: #bbb0da;
    }
    a:nth-child(8).van-sidebar-item--select {
      background: #6da6d4;
    }
    a:nth-child(9).van-sidebar-item--select {
      background: #f96c8f;
    }
    a::before {
      display: none;
    }
  }
  .re {
    box-shadow: 0px 0px 5px rgba(0, 0, 0, .1) ; 
    float: left;
    padding-top: 10px;
    padding-left: 10px;
    background-color: #fff;
    width: 265px;
    .cell {
      width: 265px;
      height: 110px;
      padding: 0 7px 11px;
      margin-bottom: 15px;
      display: flex;
      .van-cell__value {
        display: flex;
        img {
          width: 70px;
          height: 100px;
          margin-right: 10px;
        }
        .cell-box {
          width: 171px;
          height: 100px;
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          .cell-box-s {
            color: #333;
            font-weight: 600;
            word-spacing: 0px;
            line-height: 15px;
            font-size: 13px;
          }
          .cell-box-z {
            width: 143px;
            height: 45px;
            position: relative;
            display: flex;
            .cell-box-z-noe {
              display: flex;
              align-items: center;
              height: 20px;
              margin-bottom: 5px;

              img {
                border-radius: 50%;
                width: 20px;
                height: 20px;
                margin-right: 10px;
              }
              span {
                font-weight: 0;
                color: #666;
                font-size: 12px;
              }
            }
            .cell-box-z-two {
              display: flex;
              justify-content: center;
              font-size: 12px;
              height: 20px;
              line-height: 20px;
              font-weight: 300;
              color: #2376ba;
            }
            &::after {
              position: absolute;
              display: none;
              right: -28px;
              content: "";
              width: 28px;
              height: 36px;
              background: url(@/assets/img/one.png) no-repeat;
              background-size: 100% 100%;
            }
          }

          .cell-box-x {
            line-height: 13px;
            height: 13px;
            display: inline;
            font-size: 12px;
            color: #333;
          }
        }
      }
    }
    .cell:nth-child(1) .cell-box > .cell-box-z::after {
      display: block;
    }
    .cell:nth-child(2) .cell-box > .cell-box-z::after {
      display: block;
      background-image: url(@/assets/img/two.png);
    }
    .cell:nth-child(3) .cell-box > .cell-box-z::after {
      display: block;
      background-image: url(@/assets/img/three.png);
    }
  }
}
</style>